.c-avatar {
  font-size: 16px;
  height: 32px;
  width: 32px;
  color: white;
  display: inline-block;
  text-align: center;
  position: relative;
  vertical-align: middle;
  background: #1c7cd6;
  border-radius: 200px;
  font-weight: 400;
  line-height: normal; }
  .c-avatar i {
    font-size: 16px;
    line-height: 32px; }
  .c-avatar-square {
    border-radius: 0; }
  .c-avatar img {
    position: relative;
    border-radius: 200px;
    height: 100%;
    width: 100%;
    z-index: 0; }
  .c-avatar[data-status]::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    min-height: .5em;
    min-width: .5em;
    background-clip: padding-box;
    border-radius: 200px;
    box-shadow: 0 0 0 1px white;
    color: white;
    z-index: 1; }
  .c-avatar[data-badge]::after {
    position: absolute;
    top: 14.64%;
    right: 14.64%;
    transform: translate(50%, -50%);
    background: #1c7cd6;
    border-radius: 200px;
    z-index: 1;
    content: attr(data-badge);
    box-shadow: 0 0 0 1px white;
    font-size: 10px;
    height: 1rem;
    line-height: 1;
    min-width: 1rem;
    padding: 0.125rem 0.25rem;
    text-align: center;
    white-space: nowrap; }
  .c-avatar[data-text]::before {
    color: currentColor;
    content: attr(data-text);
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 0; }

