.c-checkbox,
.c-radio {
  display: inline-block;
  position: relative;
  cursor: pointer;
  line-height: 1; }
  .c-checkbox input,
  .c-radio input {
    position: absolute;
    width: 24px;
    height: 24px;
    left: 0;
    opacity: 0;
    z-index: 1;
    cursor: pointer; }
    .c-checkbox input:checked + i::before,
    .c-radio input:checked + i::before {
      border-width: 0;
      background-color: inherit; }
    .c-checkbox input:checked + span .active,
    .c-radio input:checked + span .active {
      display: inherit; }
    .c-checkbox input[type="radio"] + i, .c-checkbox input[type="radio"] + i::before,
    .c-radio input[type="radio"] + i,
    .c-radio input[type="radio"] + i::before {
      border-radius: 50%; }
    .c-checkbox input[type="checkbox"]:checked + i::after,
    .c-radio input[type="checkbox"]:checked + i::after {
      transform: rotate(45deg);
      position: absolute;
      left: 8px;
      top: 3px;
      display: table-cell;
      width: 8px;
      height: 16px;
      border: 3px solid;
      border-top: 0;
      border-left: 0;
      content: ' ';
      color: white; }
    .c-checkbox input[type="radio"]:checked + i::after,
    .c-radio input[type="radio"]:checked + i::after {
      position: absolute;
      display: table;
      left: 6px;
      top: 6px;
      width: 12px;
      height: 12px;
      background: white;
      border-radius: 50%;
      content: ' ';
      color: white; }
    .c-checkbox input[disabled] + i::before,
    fieldset[disabled] .c-checkbox input + i::before,
    .c-radio input[disabled] + i::before,
    fieldset[disabled]
    .c-radio input + i::before {
      opacity: 0.5;
      background: #dee2e6;
      cursor: not-allowed; }
    .c-checkbox input[disabled]:checked + i::before,
    .c-radio input[disabled]:checked + i::before {
      opacity: 0.5;
      cursor: not-allowed;
      border: 1px solid #adb5bd; }
    .c-checkbox input[disabled]:checked + i::after,
    .c-radio input[disabled]:checked + i::after {
      color: #adb5bd; }
  .c-checkbox > i,
  .c-radio > i {
    height: 24px;
    display: inline-block;
    margin: 0 24px 0 0;
    position: relative; }
    .c-checkbox > i::before,
    .c-radio > i::before {
      content: "";
      position: absolute;
      width: 24px;
      height: 24px;
      border: 1px solid #dee2e6;
      border-radius: 2px;
      transition: all 0.2s; }

.c-checkbox-sm,
.c-radio-sm {
  height: 16px; }
  .c-checkbox-sm input,
  .c-radio-sm input {
    width: 16px;
